import { Metadata } from "next";
import { getServerSession } from "next-auth";
import { authOptions } from "@/app/api/auth/[...nextauth]/route";
import { redirect } from "next/navigation";
import { Role } from "@prisma/client";
import { DashboardHeader } from "@/components/header";
import { DashboardShell } from "@/components/shell";
import { ClassDetailContent } from "./class-detail-content";

export const metadata: Metadata = {
  title: "Class Details | EduAsses",
  description: "View class details, announcements, materials, and quizzes",
};

interface PageProps {
  params: {
    classId: string;
  };
}

export default async function ClassDetailsPage({ params }: PageProps) {
  const session = await getServerSession(authOptions);
  
  if (!session) {
    redirect("/auth/signin");
  }
  
  if (session.user.role !== Role.STUDENT) {
    redirect("/dashboard");
  }
  
  return (
    <DashboardShell>
      <DashboardHeader
        heading="Class Details"
        text="View class details, announcements, materials, and quizzes"
      />
      <ClassDetailContent classId={params.classId} />
    </DashboardShell>
  );
} 